
import { defineComponent } from 'vue';
import Menu from '@/components/Menu.vue';
import ScrollTo from '@/lib/scrollTo';

export default defineComponent({
  name: 'Header',
  components: {
    Menu,
  },

  props: {
    title: {
      type: String,
      default: null,
    },

    description: {
      type: String,
      default: null,
    },

    actions: {
      type: Array,
      default: () => {},
    },

    project: {
      type: String,
      default: null,
    },

    about: {
      type: String,
      default: null,
    },

    contact: {
      type: String,
      default: null,
    },
  },

  setup() {
    const scrollTo = ScrollTo;

    return {
      scrollTo,
    };
  },
});
